@import url(https://fonts.googleapis.com/css?family=Dosis|Montserrat|Roboto&display=swap);
div#root{
  position: relative;
  overflow:hidden;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

nav.desktop {
  display: none;
  order: -1;
  font-family: 'Montserrat', sans-serif;
}
.hamburger {
  position: fixed;
  right: 0;
  font-size: 3rem;
  cursor: pointer;
  color: white;
  margin: 3vh;
  z-index: 99999 !important;
}

a{
  color:white;
  text-decoration: none;
}

@media (min-width: 320px) {
  i.hamburger {
    font-size: 5rem;
    text-shadow: 0 0 4px black;
  }
}
@media (min-width: 520px) {
  i.hamburger {
    font-size: 5rem;
  }
}

@media (min-width: 1000px) {
  i.hamburger {
    display: none;
  }
  nav.desktop {
    display: flex;
    position: absolute;
    top: 0;
    z-index: 5;
  }
  .desktop ul {
    position: fixed;
    display: flex;
    justify-content: center;
    list-style-type: none;
    width: 100%;
    text-transform: uppercase;
    background-color: #242830;
    color: rgb(201, 201, 201);
  }
  .desktop ul li {
    min-width: 10vw;
    cursor: pointer;
    margin: 1vh 3vh;
    padding: 1vh;
    font-size: 1.5rem;
    -webkit-transition: 0.2s;
    transition: 0.2s;
  }
  .desktop ul li:hover {
    color: white;
    text-shadow: 0 0 2px white;
    }
}

.app-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #282c34;
  text-align: center;
  color: white;
  font-family: 'Dosis', sans-serif;
}

.app-header p {
  font-size: 5rem;
  margin: 2vh auto;
  font-weight: bold;
}

.app-header h1 {
  font-size: 5.5rem;
  margin: 3vh auto;
  font-weight: normal;
}

.app-header hr {
  width: 40vw;
}

@media (min-width: 700px) {
  .app-header{
    min-height: 80vh;
  }
  .app-header p {
    font-size: 5rem;
  }
  .app-header h1 {
    font-size: 6rem;
  }
}

@media (min-width: 1025px) {
  .app-header{
    min-height: 80vh;
   
  }
  .app-header p {
    font-size: 4rem;
  }
  .app-header h1 {
    font-size: 5rem;
  }
}
