footer {
  text-align: center;
  background-color: #282c34;
  color: white;
  padding: 4vh;
  text-transform: uppercase;
  font-family: 'Dosis', sans-serif;
  font-size:2.3rem;
}

footer p:nth-of-type(1) {
  text-transform: none;
}

footer p:nth-last-of-type(1) {
  margin-top: 6vh;
  font-size: 1.2rem;
}

footer h3.contact {
  font-size: 2.5rem;
  margin-bottom: 1.5vh;
}

footer p.icons a {
  color:rgb(206, 206, 206);

}

footer p.icons a i {
  margin: 1vh 2vh;
  font-size: 5rem;
  cursor: pointer;
  -webkit-transition: .2s;
  transition: .2s;
}

footer p.icons a i:hover{
  color: white;

  -webkit-transform: scale(1.1);

          transform: scale(1.1);
-webkit-animation: 0.8s linear ding infinite;
        animation: 0.8s linear ding infinite;
}

@-webkit-keyframes ding{
0%{ -webkit-transform: rotate(0deg); transform: rotate(0deg);}
25%{
  -webkit-transform: rotate(-10deg);
          transform: rotate(-10deg)
}
50%{
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}
75%{
  -webkit-transform: rotate(10deg);
          transform: rotate(10deg)
}
100%{
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg)
}
}

@keyframes ding{
0%{ -webkit-transform: rotate(0deg); transform: rotate(0deg);}
25%{
  -webkit-transform: rotate(-10deg);
          transform: rotate(-10deg)
}
50%{
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}
75%{
  -webkit-transform: rotate(10deg);
          transform: rotate(10deg)
}
100%{
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg)
}
}
@media(min-width: 600px){
  footer h3.contact {
    font-size: 4rem;
  
  }
  footer p.icons a i {
  
    font-size: 4.5rem;
    
  }
  
}

@media(min-width: 800px){
  footer h3.contact {
    font-size: 3rem;
  
  }
  footer p.icons a i {
  
    font-size: 5rem;
    
  }
  
}

@media(min-width: 1000px){
  footer h3.contact {
    font-size: 3rem;
  }
  footer p.icons a i {
    font-size: 4rem;
  }
  footer p:nth-last-of-type(1) {
    font-size: 1rem;
  }
  footer p:nth-of-type(1),
  footer p:nth-of-type(2){
    font-size: 2rem;
  }
}

@media(min-width: 1300px){
  footer h3.contact {
    font-size: 4rem;
  }
  footer p.icons a i {
    font-size: 5rem;
  }
  footer p:nth-last-of-type(1) {
    font-size: 1.5rem;
  }
  footer p:nth-of-type(1),
  footer p:nth-of-type(2){
    font-size: 3rem;
  }
}
nav > div.on {
  position: fixed;
  background-color: rgba(248, 248, 248, 0.98);
  top:0;
  bottom:0;
  right:20%;
  left: 100%;
  padding: 50pt;
  overflow: hidden;
 z-index: 10;
 -webkit-transition: .3s;
 transition: .3s;
 font-family: 'Dosis', sans-serif;
}
div.on a {
  color:#656f83;
}
div.on a:hover {
  color:black;
}
div.on.active{
  left: 0;
}
div.on ul{
  list-style-type: none;
  font-size: 3rem;
  text-transform: uppercase;
  
}
div.on ul li {
  
  padding: 5pt;
  cursor: pointer;
  -webkit-transition: .2s;
  transition: .2s;
}
div.on ul li:hover{

 -webkit-transform: translateX(5%);

         transform: translateX(5%);
}
@media(min-width: 500px){
    div.on.active{
      right: 35%;
    }
}
@media(min-width: 700px){
    div.on.active{
      right: 50%;
    }
}
@media(min-width: 850px){
 
    div.on.active{
      right: 55%;
    }
}

@media(min-width: 1000px){
div.on{
  display: none;
}
}
